	import back from '@/components/back'

	export default {
		components: {
			back
		},
		data() {
			return {
				title: "",
				isRecommend: false,
				//list:null,
				list: null,
				recomMsg:null,
				info:null,
				fromIM:this.$store.state.recomMsg.fromIM
			}
		},
		created() {
			if(this.$store.state.editRecomDetails) {
				this.info = this.$store.state.editRecomDetails
				this.list = this.$store.state.editRecomDetails.ListDetails
			} else {
				this.list = []
			}
			if(this.$store.state.recomMsg){
				this.recomMsg = this.$store.state.recomMsg
			}
		},
		mounted() {

		},
		beforeDestroy() {
			this.$store.dispatch('pushEditRecomDetails', null)
			window.onscroll = null
		},
		methods: {
			/*addGoods(){
				//
			},*/
			subNum(n) {
				//1
				if(this.list[n].GoodsNum > 1) {
					this.list[n].GoodsNum--
				}
			},
			addNum(n) {
				//1
				this.list[n].GoodsNum++
			},
			deleteItem(n) {
				//
				if(this.list.length > 1) {
					this.list.splice(n, 1)
				} else {
					this.$toast("")
				}
			},
			saveGoods() {
				/*this.recomMsg = {
					DataSource: 2,//(123)
					DataId: 11,//Id
				}*/
				let _url = "PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommend"
// 				if(this.recomMsg && this.recomMsg.DataSource == 1){
// 					//
// 					_url = "PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommendUsed"
// 				}else if(this.recomMsg && this.recomMsg.DataSource == 2){
// 					//
// 					_url = "PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommend"
// 				}else{
// 					
// 				}
				let ListArr = []
				this.list.forEach(v => {
					ListArr.push({
						GoodsSource:v.GoodsSource?v.GoodsSource:3,	//(1-2-3-)
						GoodsId:v.GoodsId,	//Id/Id
						SpecialistGoodsId:v.GoodsSource == 3 ? 0:v.SpecialistGoodsId,//Id(1,2,4 30)
						SpecialistHosGroupId:v.SpecialistHosGroupId?v.SpecialistHosGroupId:0, //id
						HosGroupName:v.SpecialistHosGroupId?v.HosGroupName:'',  //
						GoodsName:v.GoodsName,	///
						GoodsNum:v.GoodsNum,	//
						DrId:v.DrId || 0,
						DrName:v.DrName||'',
						GoodsSpecId:v.GoodsSpecId || "",	//Id
						GoodsSpecName:v.GoodsSpecName || "",	//
					})
				})
				this.$post(_url, {
					GoodsRecomId:0,	//Id  this.info.GoodsRecomId,
					DrId: this.info.DrId,
					DrName: this.info.DrName,
					GoodsRecomDetail: ListArr
				}).then(o => {
					if(!o){
						return
					}
					this.$store.dispatch('pushrecomMsg',{
						DataSource: 2,//(123)
						DataId: o,//Id
						fromIM:this.fromIM
					})
					this.$router.replace(`/prefRecommend/reDetail?tId=1`)
				})
			}
		}
	}



